import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY;
const supabaseServiceKey = process.env.REACT_APP_SUPABASE_SERVICE_KEY; // DEV-ONLY bypass

if (!supabaseUrl || !supabaseAnonKey) {
  throw new Error('Missing Supabase environment variables');
}

// Regular client for normal operations (anon key only)
export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    persistSession: true,
    autoRefreshToken: true,
    detectSessionInUrl: true
  }
});

// DEV-ONLY: optional admin client using service key for local admin dashboard convenience
// NOTE: This is insecure for production, but acceptable for your local-only setup per instructions.
export const supabaseAdmin = supabaseServiceKey
  ? createClient(supabaseUrl, supabaseServiceKey, {
      auth: { persistSession: false, autoRefreshToken: false }
    })
  : null;

export const hasAdminBypass = !!supabaseAdmin;

/**
 * Admin Create User - Secure Edge Function Wrapper
 */
export async function adminCreateUser({ email, password, firstName, lastName, role, jobTitle }) {
  const { data: session } = await supabase.auth.getSession();
  if (!session?.session) {
    throw new Error('Not authenticated');
  }

  const { data, error } = await supabase.functions.invoke('admin-create-user', {
    body: { email, password, firstName, lastName, role, jobTitle }
  });

  if (error) throw error;
  return data;
}

/**
 * Admin Delete User - Secure Edge Function Wrapper
 */
export async function adminDeleteUser(userId) {
  const { data: session } = await supabase.auth.getSession();
  if (!session?.session) {
    throw new Error('Not authenticated');
  }

  const { data, error } = await supabase.functions.invoke('admin-delete-user', {
    body: { userId }
  });

  if (error) throw error;
  return data;
}

/**
 * Create Company with Owner - Secure Edge Function Wrapper
 */
export async function createCompanyWithOwner({ companyName, ownerFirstName, ownerLastName, ownerEmail, ownerPhone, tempPassword }) {
  const { data, error } = await supabase.functions.invoke('create-company-with-owner', {
    body: { companyName, ownerFirstName, ownerLastName, ownerEmail, ownerPhone, tempPassword }
  });

  if (error) throw error;
  return data;
}

console.log('✅ Supabase client initialized');
console.log('📍 URL:', supabaseUrl);
console.log(hasAdminBypass
  ? '🔓 DEV ADMIN BYPASS ENABLED (service key present)'
  : '🔒 Admin operations routed through Edge Functions');

/**
 * Admin List Users - Secure Edge Function Wrapper
 */
export async function adminListUsers(params = {}) {
  const { data: session } = await supabase.auth.getSession();
  if (!session?.session) {
    throw new Error('Not authenticated');
  }
  const { data, error } = await supabase.functions.invoke('admin-list-users', {
    body: params
  });
  if (error) throw error;
  return data;
}

/**
 * DEV-ONLY: Admin List Users using local service key bypass (no Edge Function needed)
 */
export async function adminListUsersLocal(params = {}) {
  if (!supabaseAdmin) throw new Error('Admin bypass not configured');
  const limit = Math.min(Math.max(parseInt(String(params.limit ?? 200)), 1), 1000);
  const search = (params.search ?? '').trim();

  let usersQuery = supabaseAdmin
    .from('users')
    .select('id, email, role, status, created_at, company_id')
    .order('created_at', { ascending: false })
    .limit(limit);

  if (search) {
    usersQuery = usersQuery.or(`email.ilike.%${search}%`);
  }

  const { data: users, error: usersErr } = await usersQuery;
  if (usersErr) throw usersErr;

  const userIds = users.map(u => u.id);
  const companyIds = [...new Set(users.map(u => u.company_id).filter(Boolean))];

  const [profilesRes, companiesRes] = await Promise.all([
    userIds.length
      ? supabaseAdmin.from('profiles').select('id, first_name, last_name, phone').in('id', userIds)
      : Promise.resolve({ data: [] }),
    companyIds.length
      ? supabaseAdmin.from('companies').select('id, name').in('id', companyIds)
      : Promise.resolve({ data: [] })
  ]);

  const profiles = profilesRes.data || [];
  const companies = companiesRes.data || [];

  const profileMap = {};
  profiles.forEach(p => { profileMap[p.id] = p; });

  const companyMap = {};
  companies.forEach(c => { companyMap[c.id] = c; });

  const result = users.map(u => {
    const p = profileMap[u.id] || {};
    const c = companyMap[u.company_id] || null;
    const display_name = [p.first_name, p.last_name].filter(Boolean).join(' ').trim();
    return {
      id: u.id,
      email: u.email,
      role: u.role,
      status: u.status,
      created_at: u.created_at,
      company_id: u.company_id,
      company: c ? { id: c.id, name: c.name } : null,
      display_name,
      phone: p.phone || null,
    };
  });

  return { users: result };
}
