import React, { useState, useEffect } from 'react';
import { supabase, adminListUsers, adminListUsersLocal, hasAdminBypass } from '../supabaseClient';

const UserList = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = async () => {
    try {
      setLoading(true);
      setError(null);

      console.log('🔍 Fetching all users...');

      // 1) DEV-ONLY: If service key is present, use local admin bypass (no Edge Function needed)
      if (hasAdminBypass) {
        try {
          const adminData = await adminListUsersLocal({ limit: 500 });
          const rows = Array.isArray(adminData?.users) ? adminData.users : [];
          if (rows.length > 0) {
            setUsers(rows);
            return;
          }
        } catch (e) {
          console.warn('Admin bypass failed, will try Edge Function next:', e?.message || e);
        }
      }

      // 2) Prefer privileged Edge Function for APP_OWNER to see all users
      try {
        const adminData = await adminListUsers({ limit: 500 });
        let rows = Array.isArray(adminData?.users) ? adminData.users : Array.isArray(adminData) ? adminData : [];
        if (rows.length > 0) {
          // Normalize fields expected by UI
          const normalized = rows.map(r => {
            const profile = r.profile || r.profiles || {};
            const company = r.company || r.companies || (r.company_name ? { name: r.company_name, id: r.company_id } : null);
            const display_name = r.display_name || r.full_name || [profile.first_name, profile.last_name].filter(Boolean).join(' ').trim();
            return {
              id: r.id,
              email: r.email,
              role: r.role,
              status: r.status,
              created_at: r.created_at,
              company_id: r.company_id,
              company,
              display_name,
              phone: r.phone || profile.phone || null,
            };
          });
          setUsers(normalized);
          return;
        }
      } catch (e) {
        console.warn('Admin Edge Function not available or failed, falling back to direct queries:', e?.message || e);
      }

      // 3) Fallback: Query minimal, guaranteed columns only (RLS may restrict results)
      const { data: users, error } = await supabase
        .from('users')
        .select('id, email, role, status, created_at, company_id')
        .order('created_at', { ascending: false});

      if (error) {
        console.error('❌ Database query failed:', error);
        throw new Error(`Failed to fetch users: ${error.message}`);
      }

      console.log('✅ Users fetched successfully:', users?.length || 0);

      // Enrich with profile names/phone using a separate query with select('*')
      if (users && users.length > 0) {
        const userIds = users.map(u => u.id);
        const { data: profiles, error: profileErr } = await supabase
          .from('profiles')
          .select('*')
          .in('id', userIds);
        if (!profileErr && Array.isArray(profiles)) {
          const profileMap = {};
          profiles.forEach(p => { profileMap[p.id] = p; });
          users.forEach(u => {
            const p = profileMap[u.id] || {};
            const fullName = [p.first_name, p.last_name].filter(Boolean).join(' ').trim();
            if (fullName) u.display_name = fullName;
            if (p.phone) u.phone = p.phone;
          });
        }

        // Fetch company names separately to avoid PostgREST relationship issues
        const companyIds = [...new Set(users.map(u => u.company_id).filter(Boolean))];
        if (companyIds.length > 0) {
          const { data: companies } = await supabase
            .from('companies')
            .select('id, name')
            .in('id', companyIds);
          const companyMap = {};
          companies?.forEach(c => { companyMap[c.id] = c; });
          users.forEach(u => { u.company = companyMap[u.company_id] || null; });
        }
      }

      setUsers(users || []);

    } catch (error) {
      console.error('Error loading users:', error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  const getRoleBadgeColor = (role) => {
    switch (role) {
      case 'APP_OWNER': return 'bg-purple-100 text-purple-800';
      case 'OWNER': return 'bg-blue-100 text-blue-800';
      case 'ADMIN': return 'bg-green-100 text-green-800';
      case 'EMPLOYEE': return 'bg-gray-100 text-gray-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        <span className="ml-3 text-gray-600">Loading users...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <div className="flex">
          <div className="ml-3">
            <h3 className="text-sm font-medium text-red-800">Error Loading Users</h3>
            <div className="mt-2 text-sm text-red-700">
              <p>{error}</p>
            </div>
            <div className="mt-4">
              <button
                onClick={loadUsers}
                className="bg-red-100 px-3 py-2 rounded-md text-sm font-medium text-red-800 hover:bg-red-200"
              >
                Try Again
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">User Profiles</h1>
          <p className="mt-1 text-sm text-gray-500">
            All user profiles in the system
          </p>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-white overflow-hidden shadow rounded-lg">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className="w-8 h-8 bg-purple-500 rounded-md flex items-center justify-center">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                  </svg>
                </div>
              </div>
              <div className="ml-5 w-0 flex-1">
                <dl>
                  <dt className="text-sm font-medium text-gray-500 truncate">Total Users</dt>
                  <dd className="text-lg font-medium text-gray-900">{users.length}</dd>
                </dl>
              </div>
            </div>
          </div>
        </div>
        
        <div className="bg-white overflow-hidden shadow rounded-lg">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className="w-8 h-8 bg-blue-500 rounded-md flex items-center justify-center">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
              </div>
              <div className="ml-5 w-0 flex-1">
                <dl>
                  <dt className="text-sm font-medium text-gray-500 truncate">Owners</dt>
                  <dd className="text-lg font-medium text-gray-900">
                    {users.filter(u => u.role === 'OWNER' || u.role === 'APP_OWNER').length}
                  </dd>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Users Table */}
      <div className="bg-white shadow overflow-hidden sm:rounded-md">
        <div className="px-4 py-5 sm:px-6">
          <h3 className="text-lg leading-6 font-medium text-gray-900">All User Profiles</h3>
          <p className="mt-1 max-w-2xl text-sm text-gray-500">
            Complete list of user profiles from the profiles table
          </p>
        </div>
        
        {users.length === 0 ? (
          <div className="text-center py-12">
            <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
            </svg>
            <h3 className="mt-2 text-sm font-medium text-gray-900">No users found</h3>
            <p className="mt-1 text-sm text-gray-500">No user profiles in the database.</p>
          </div>
        ) : (
          <ul className="divide-y divide-gray-200">
            {users.map((user) => {
              const displayName = user.display_name || user.email || 'Unknown User';
              const initial = displayName.charAt(0).toUpperCase();

              return (
              <li key={user.id} className="px-4 py-4 sm:px-6">
                <div className="flex items-center justify-between">
                  <div className="flex items-center">
                    <div className="flex-shrink-0">
                      <div className="h-10 w-10 bg-gray-100 rounded-full flex items-center justify-center">
                        <span className="text-sm font-medium text-gray-600">
                          {initial}
                        </span>
                      </div>
                    </div>
                    <div className="ml-4">
                      <div className="text-sm font-medium text-gray-900">
                        {displayName}
                      </div>
                      <div className="text-sm text-gray-500">
                        {user.email}
                      </div>
                      {user.phone && (
                        <div className="text-sm text-gray-500">
                          📞 {user.phone}
                        </div>
                      )}
                      <div className="text-sm text-gray-500">
                        Company: {user.company?.name || 'No company'}
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center space-x-4">
                    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getRoleBadgeColor(user.role)}`}>
                      {user.role}
                    </span>
                    <div className="text-sm text-gray-500">
                      {formatDate(user.created_at)}
                    </div>
                  </div>
                </div>
              </li>
              );
            })}
          </ul>
        )}
      </div>
    </div>
  );
};

export default UserList;
